import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import CreateAccount from "./components/create-account.component";
import LoginAccount from "./components/login-account.component";

function App() {
  return (
    <Router>
      <div className="bg-primary d-flex" style={{ height: "100vh" }}>
        <Route path="/" exact component={LoginAccount} />
        <Route path="/create" exact component={CreateAccount} />
      </div>
    </Router>
  );
}

export default App;
